<template>
  <el-button :size="size" :type="type" :icon="icon"
    :loading="loading"     v-if="hasPerms(perms)" @click.stop="stopClick"> <!-- @click="handleClick"  :disabled="!hasPerms(perms)"-->
    {{label}}
  </el-button>
</template>
<!--:disabled="!hasPerms(perms)"-->
<script>
import { hasPermission } from '@/permission/index.js'
export default {
  name: 'CbButton',
  props: {
    label: {  // 
      type: String,
      default: ''  /*Button*/
    },
    icon: {  // 
      type: String,
      default: ''
    },
    size: {  // 
      type: String,
      default: 'mini'
    },
    type: {  // 
      type: String,
      default: null
    },
    loading: {  // 
      type: Boolean,
      default: false
    },
    disabled: {  // 
      type: Boolean,
      default: false
    },
    perms: {  // 
      type: String,
      default: null
    }
  },
  data() {
    return {
    }
  },
  methods: {
    handleClick: function () {
      // 
      this.$emit('click', {})
    },
    stopClick: function () {
      // 
      this.$emit('click', {})
    },
    hasPerms: function (perms) {
      // 
      return hasPermission(perms) & !this.disabled
    }
  },
  mounted() {
  }
}
</script>

<style scoped>

</style>



